<template>
  <div class="product_list">
    <div class="product_main">
      <cata-log :page="page"></cata-log>
      <div class="task">
        <div class="task_intro">
          <div class="task_title">任务介绍</div>
          <div class="task_info">
            <div class="task_col">
              <div class="task_row">任务号：10000000000</div>
              <div class="task_row">申请人：韩梅梅</div>
            </div>
            <div class="task_col">
              <div class="task_row">项目：这里是项目内容</div>
              <div class="task_row">电话：021-82872839</div>
            </div>
          </div>
        </div>
        <div class="task_list">
          <div class="infomation" v-for="(item, index) of 16" :key="index">
            <img class="infomation_icon" src="../../assets/img/group.png" />
            <div class="infomation_txt">
              海关总署
              国家外汇管理局公告2019年93号（关于取消报关单收、付汇证明联和海关核销联的公告）
            </div>
            <div class="infomation_date">2020-05-27</div>
          </div>
        </div>
        <div class="paginaton">
          <el-pagination
            :background="false"
            :pager-count="9"
            layout="prev, pager, next"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="80"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import CataLog from "/page/mine/catalog";
export default {
  data() {
    return {
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
      value: "",
      date: "",
      page: "task"
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this._getSearch();
      this.loading = true;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this._getSearch();
      this.loading = true;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
  created() {},
  components: {
    CataLog
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f0faff;
  width: 1440px;
  margin: 0 auto;
  padding-top: 24px;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
  display: flex;
  .task {
    width: 946px;
    .task_intro {
      width: 900px;
      height: 146px;
      margin: 24px;
      background: #fafafa;
      border-radius: 2px;
      padding: 24px;
      .task_title {
        font-size: 20px;
        color: #333333;
        line-height: 28px;
        font-weight: bold;
      }
      .task_info {
        display: flex;
        .task_col {
          width: 250px;
          .task_row {
            margin-top: 10px;
            font-size: 18px;
            color: #333333;
          }
        }
      }
    }
    .task_list {
      width: 946px;
      border-top: 1px solid rgba(225, 225, 225, 0.5);
      padding: 0 24px;
      .infomation {
        height: 46px;
        line-height: 46px;
        border-bottom: 1px dashed #dadada;
        font-size: 16px;
        color: #333333;
        display: flex;
        .infomation_icon {
          width: 6px;
          height: 12px;
          position: relative;
          top: 16px;
        }
        .infomation_txt {
          margin-left: 20px;
          width: 650px;
          font-weight: bold;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .infomation_date {
          margin-left: 20px;
          width: 200px;
          text-align: right;
        }
      }
    }
  }
  .paginaton {
    height: 92px;
    text-align: right;
    padding: 24px;
  }
}
</style>



